import { useState } from 'react';
import styles from './HelpPage.module.scss'

const HelpPage = () => {
  const isAlreadyCorrect = !!localStorage.getItem('protocol')
  const [protocolValue, setProtocolValue] = useState(
    isAlreadyCorrect ?
    ' ' : ''
  )
  const [showSecret, setShowSecret] = useState(isAlreadyCorrect)

  const words = ['', '', '']
  const english = new Set('abcdefghijklmnopqrstuvwxyz')
  const set = new Set(words.join(''))

  function inputChangeHandler(evt: React.ChangeEvent<HTMLInputElement>) {

    let k = 0
    const cur = new Set(evt.target.value.toLowerCase())

    for (let el of Array.from(cur)) {
      if (english.has(el)) {
        setProtocolValue(`     `)
        return
      }
    }

    for (let word of words) {
      if (evt.target.value.toLowerCase().includes(word)) {
        k += 1
      }
    }

    if (k === 1) {
      setProtocolValue(`  1  `)
    } else if (k === 2) {
      setProtocolValue(`  ${k}  `)
    } else if (k === 3) {
      if (evt.target.value.length < words.join('').length + words.length - 1) {
        setProtocolValue('  ')
      } else {
        setProtocolValue(` `)
        evt.target.disabled = true
        setShowSecret(true)
        localStorage.setItem('protocol', evt.target.value)
      }
    } else {
      if (evt.target.value) {
        let j = 0
        
        set.forEach(el => {
          if (cur.has(el)) {
            j += 1
          }  
        })
        
        if (j === 1) {
          setProtocolValue(`  1  `)
        } else if (j > 1 && j < 5) {
          setProtocolValue(`  ${j}  `)
        } else if (j >= 5) {
          setProtocolValue(`  ${j}  `)
        } else {
          setProtocolValue(` `)
        }
      } else {
        setProtocolValue(``)
      }
    }
  }

  return (
    <div className={styles.container}>
      <p>
          -  .     ,      
      </p>
      <p>
         ,    ,   -       
      </p>

      <p>
      <u>  </u> () -  ,     ,        ,     ,      <u>  </u> ()     
      </p>

      <p>
        <u></u> -   ,     
      </p>

      <p>
         ,         <u></u> (  ). ,   <u> </u>,  <u></u>     
      </p>

      <p>
           ,        , , -,   <u></u>, -,         
      </p>

      <p>
        ,         ,  <u> </u>
      </p>

      <p>
         ,   ,         ,      TimeStamp.     ...
      </p>

      <hr />

      <p>
         C-16-O.      TimeStamp,         T-16-G,        <b></b>  ,       . C-16-O         T-16-G   .   ,    ...
      </p>

      <p>
        C-16-O       
      </p>

      <input 
        type="text"
        name="protocol"
        placeholder=' ...'
        defaultValue={localStorage.getItem('protocol') ?? ''}
        disabled={isAlreadyCorrect}
        onChange={inputChangeHandler}
      />

      <p>
        {protocolValue}
      </p>

      {showSecret ?
        <>
          <hr />
          <p>
             T-16-G              ,     
          </p>

          <p>
                   :   .      .     ,         ,  
          </p>

          <p>
              ,   TimeStamp  , ,      ,        ,    .       ,   ,       ...
          </p>
        </>
        : <div className={styles.empty}></div>
      }

    </div>
  );
};

export default HelpPage;