export const getAnomalyText = (cts: 1 | 2) => {
  const anomalies = {
    1: [
      ' T-16-G       1  ...',
      ' T-16-G       -  ,        ...',
      '-      ,   T-16-G...'
    ],
    2: [
      ' T-16-G       2  ...',
      ' T-16-G       -  ,        ...',
      '-      ,   T-16-G...'
    ]
  }
  return anomalies[cts][Math.floor(Math.random() * 2.99)]
}